
@font-face {
    font-family: 'Spartan';
    src: url(../fonts/Spartan-VariableFont_wght.ttf);
}

:root {
    --clr-fiord: /* hsl(222, 26%, 31%) */  rgba(58, 71, 100, 1);
    --clr-mercury: /* hsl(0, 0%, 90%) */ rgba(230, 230, 230, 1);
    --clr-very-dark-violet: /* hsl(268, 75%, 9%) */ rgba(22, 6, 40, 1);
    --clr-ebony-clay: /* hsl(223, 31%, 20%) */ rgba(35, 44, 67, 1);
    --clr-swiss-cofee: /* hsl(0, 5%, 81%) */ rgba(209, 204, 204, 1);
    --clr-dark-violet: /* hsl(268, 71%, 12%) */ rgba(29, 9, 52, 1);
    --clr-mirage: /* hsl(224, 36%, 15%) */ rgba(24, 32, 52, 1);
    --clr-gallery: /* hsl(0, 0%, 93%) */ rgba(237, 237, 237, 1);
    --clr-windsor: /* hsl(281, 89%, 26%) */ rgba(88, 7, 125, 1);
    --clr-lynch: /* hsl(225, 21%, 49%) */ rgba(99, 112, 151, 1);
    --clr-persian-red: /* hsl(6, 63%, 50%) */ rgba(208, 63, 47, 1);
    --clr-pearl-bush: /* hsl(30, 25%, 89%) */ rgba(234, 227, 220, 1);
    --clr-paradiso: /* hsl(185, 42%, 37%) */ rgba(55, 127, 134, 1);
    --clr-burnt-orange: /* hsl(25, 98%, 40%) */ rgba(202, 85, 2, 1);
    --clr-westar: /* hsl(45, 7%, 89%) */ rgba(229, 228, 225, 1);
    --clr-robins-egg-blue: /* hsl(176, 100%, 44%) */ rgba(0, 224, 209, 1);
    --clr-grape: /* hsl(268, 47%, 21%) */ rgba(52, 28, 79, 1);
    --clr-east-bay: /* hsl(224, 28%, 35%) */ rgba(64, 78, 114, 1);
    --clr-old-brick: /* hsl(6, 70%, 34%) */ rgba(147, 38, 26, 1);
    --clr-napa: /* hsl(28, 16%, 65%) */ rgba(180, 165, 151, 1);
    --clr-blumine: /* hsl(185, 58%, 25%) */ rgba(27, 95, 101, 1);
    --clr-peru-tan: /* hsl(25, 99%, 27%) */ rgba(137, 57, 1, 1);
    --clr-zorba: /* hsl(35, 11%, 61%) */ rgba(166, 157, 145, 1);
    --clr-electric-violet: /* hsl(285, 91%, 52%) */ rgba(188, 21, 244, 1);
    --clr-aquamarine: /* hsl(177, 92%, 70%) */ rgba(108, 249, 242, 1);
    --clr-seance: /* hsl(290, 70%, 36%) */ rgba(135, 28, 156, 1);
    --clr-river-bed: /* hsl(221, 14%, 31%) */ rgba(68, 75, 90, 1);
    --clr-white: /* hsl(0, 0, 100%) */ rgba(255,255,255, 1);
    --clr-armadillo: /* hsl(60, 10%, 19%) */ rgba(53, 53, 44, 1);
    --clr-bright-sun: /* hsl(52, 100%, 62%) */ rgba(255, 229, 61, 1);
    --clr-shark:  /* hsl(198, 20%, 13%) */ rgba(27, 36, 40, 1);
  }


*,
*::before,
*::after {
  box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: 0;
}

html {
    font-family: 'Spartan', Segoe UI;
    font-size: 32px;
}

html[data-theme='theme-1'] {
    
    background: var(--clr-fiord);  
    --clr-toggle-bg: var(--clr-ebony-clay);
    --clr-keypad-bg: var(--clr-ebony-clay);
    --clr-screen-bg: var(--clr-mirage);

    // keys
    --clr-primary-key-bg: var(--clr-pearl-bush);
    --clr-secondary-key-bg: var(--clr-lynch);
    --clr-tertiary-key-bg: var(--clr-persian-red);

    --clr-primary-key-shadow: var(--clr-napa);
    --clr-secondary-key-shadow: var(--clr-east-bay);
    --clr-tertiary-key-shadow: var(--clr-old-brick);

    //toggle
    --clr-toggle: var(--clr-persian-red);

    //tetx 
    --clr-primary-text: var(--clr-river-bed);
    --clr-secondary-text: var(--clr-white);
    --clr-tertiary-text: var(--clr-white);
    --clr-screen-text: var(--clr-white);
}

html[data-theme='theme-2'] {
    
    background: var(--clr-mercury);  
    --clr-toggle-bg: var(--clr-swiss-cofee);
    --clr-keypad-bg: var(--clr-swiss-cofee);
    --clr-screen-bg: var(--clr-gallery);

    // keys
    
    --clr-primary-key-bg: var(--clr-westar);
    --clr-secondary-key-bg: var(--clr-paradiso);
    --clr-tertiary-key-bg: var(--clr-burnt-orange);

    --clr-primary-key-shadow: var(--clr-zorba);
    --clr-secondary-key-shadow: var(--clr-blumine);
    --clr-tertiary-key-shadow: var(--clr-peru-tan);
    

    //toggle
    --clr-toggle: var(--clr-burnt-orange);

    //tetx 
    --clr-primary-text: var(--clr-armadillo);
    --clr-secondary-text: var(--clr-white);
    --clr-tertiary-text: var(--clr-white);
    --clr-screen-text: var(--clr-armadillo);
}

html[data-theme='theme-3'] {
    
    background: var(--clr-very-dark-violet);  
    --clr-toggle-bg: var(--clr-dark-violet);
    --clr-keypad-bg: var(--clr-dark-violet);
    --clr-screen-bg: var(--clr-dark-violet);

    // keys
    --clr-primary-key-bg: var(--clr-grape);
    --clr-secondary-key-bg: var(--clr-windsor);
    --clr-tertiary-key-bg: var(--clr-robins-egg-blue);

    --clr-primary-key-shadow: var(--clr-seance);
    --clr-secondary-key-shadow: var(--clr-electric-violet);
    --clr-tertiary-key-shadow: var(--clr-aquamarine);

    //toggle
    --clr-toggle: var(--clr-robins-egg-blue);

    //tetx 
    --clr-primary-text: var(--clr-bright-sun);
    --clr-secondary-text: var(--clr-white);
    --clr-tertiary-text: var(--clr-shark);
    --clr-screen-text: var(--clr-bright-sun);
}



body {
margin: 0;
padding: 0;
}

.calc-grid { 
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 23rem 1fr;
    grid-template-rows: 1fr 3rem 5.5rem 20rem 1fr;
    gap: 0.9rem;
    
}

.calc-header {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    display: grid;
    grid-template-columns: 1fr 1fr;

}

.calc-title {
    display: flex;
    align-items: center;
    color: var(--clr-screen-text);
    font-size: 1.35em;
    font-weight: 700;
    padding-top: 0.7rem;
}

.calc-toggle-btn {
    position: relative;
}

.calc-screen {
    border-radius: 0.5rem;
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    background: var(--clr-screen-bg);
    color: var(--clr-screen-text);
    font-size: 2em;
    font-weight: 700;
    text-align: right;
    padding: 1.5rem;

}

.calc-keypad {
    border-radius: 0.5rem;
    grid-column: 2 / 3;
    grid-row: 4 / 5;
    background: var(--clr-keypad-bg);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 0.97rem;

    padding: 1.2rem 1rem;
}




//tri-state-button

.toggle-btn-wrapper {
    position: absolute;
    right: 0;
    bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 1.1rem;
    width: 2.8rem;
    background: var(--clr-toggle-bg);
    border-radius: 25px;    
}

.checkmark-area  {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 33%;
}

#checkmark {
    height: 0.68rem;
    width: 0.68rem;
    border-radius: 100%;
    background: var(--clr-toggle);
}

.checkmark-area::before {
    content: attr(data-theme);
    position: absolute;
    top: -0.8rem;
    font-size: 0.5em;
    font-weight: 700;
    color: var(--clr-screen-text);
}

.toggle-btn-wrapper::before {
    content:'theme';
    position: absolute;
    top: 0.3rem;
    left: -3.3rem;
    text-transform: uppercase;
    letter-spacing: 0.08rem;
    font-size: 0.5em;
    font-weight: 700;
    color: var(--clr-screen-text);
}


// keypad

.keypad-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--clr-primary-key-bg);
    border-radius: 0.4rem;
    padding-top: 0.2rem;
    font-size: 1.4em;
    font-weight: 700;
    color: var(--clr-primary-text);
    box-shadow: 0 0.2rem 0 var(--clr-primary-key-shadow);
    cursor: pointer;
}

//1st row

.btn-7 {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
}

.btn-8 {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
}

.btn-9 {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
}

.btn-del {
    background: var(--clr-secondary-key-bg);
    box-shadow: 0 0.2rem 0 var(--clr-secondary-key-shadow);
    color: var(--clr-secondary-text);
    font-size: 0.9em;
    text-transform: uppercase;
    grid-column: 4 / 5;
    grid-row: 1 / 2;
}

//2nd row

.btn-4 {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
}

.btn-5 {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
}

.btn-6 {
    grid-column: 3 / 4;
    grid-row: 2 / 3;
}

.btn-plus {
    grid-column: 4 / 5;
    grid-row: 2 / 3;
}

//3rd row

.btn-1 {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
}

.btn-2 {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
}

.btn-3 {
    grid-column: 3 / 4;
    grid-row: 3 / 4;
}

.btn-minus {
    grid-column: 4 / 5;
    grid-row: 3 / 4;
}

//4th row

.btn-dot {
    grid-column: 1 / 2;
    grid-row: 4 / 5;
}

.btn-0 {
    grid-column: 2 / 3;
    grid-row: 4 / 5;
}

.btn-divide {
    grid-column: 3 / 4;
    grid-row: 4 / 5;
}

.btn-multiply{
    grid-column: 4 / 5;
    grid-row: 4 / 5;
}


//5th row

.btn-reset {
    background: var(--clr-secondary-key-bg);
    box-shadow: 0 0.2rem 0 var(--clr-secondary-key-shadow);
    color: var(--clr-secondary-text);
    font-size: 0.9em;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    grid-column: 1 / 3;
    grid-row: 5 / 6;
}

.btn-result {
    background: var(--clr-tertiary-key-bg);
    box-shadow: 0 0.2rem 0 var(--clr-tertiary-key-shadow);
    color: var(--clr-tertiary-text);
    font-size: 0.9em;
    grid-column: 3 / 5;
    grid-row: 5 / 6;
}

